.React-Icon {
    height: 8vmin;
  }
@media (prefers-reduced-motion: no-preference) {
    .React-Icon {
      animation: React-Icon-spin infinite 20s linear;
    }
  }

  @keyframes React-Icon-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }


